<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-location-outline"></i> 
        </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="borderBox">
        <div class="flex headSelect" style="margin-bottom:10px;">
          <div class="flex">
            <img class="titleIcon" src="../../../assets/img/form.png" alt />
            <div style="margin-top: -4px;"></div>
          </div>

          <div class="flex funcHead">
            <div class="block">
              <span class="demonstration" style="margin-left:10px" ></span>
              <el-input style="width:140px" v-model="kName" placeholder=""></el-input>
            </div>
            <el-button
              style="margin-left:10px"
              @click="search"
              type="primary"
              icon="el-icon-search"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="exportCode()"
              type="primary"
              icon="el-icon-download"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="exportDemo()"
              type="primary"
              icon="el-icon-download"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="upload()"
              type="primary"
              icon="el-icon-upload2"
            ></el-button>
            <el-button
              style="margin-left:10px"
              @click="openAdd(1)"
              type="primary"
              icon="el-icon-plus"
            ></el-button>
          </div>
        </div>
        <el-table
            :data="tableData"
            style="width: 100%"
            v-loading="loading"
          >
          <el-table-column type="index" label="" align="center" width="55"></el-table-column>
          <el-table-column prop="name"  label="" align="center" ></el-table-column>
          <el-table-column prop="code"  label="" align="center"  >
                <template slot-scope="scope">
                        <span class="pointer"  @click="qrCode(scope.$index, scope.row)"></span>
                 </template>
          </el-table-column>
          <el-table-column prop="time"  label="" align="center"  ></el-table-column>
          <el-table-column  label="" width="100px" align="center" > 
                <template slot-scope="scope">
                        <span class="pointer"  @click="detials(scope.$index, scope.row)"></span>
                        <span class="pointer" style="margin-left:10px"  @click="deal(scope.$index, scope.row)"></span>
                 </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination
            background
            @current-change="handleCurrentChange"
            layout="total, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>

    <!--  -->
    <el-dialog :title="mTitle" :visible.sync="editVisible" width="40%"  >
        <el-form ref="ruleForm"  :model="ruleForm"  :rules="rules" label-width="120px" v-loading="loading1"  >
            <el-form-item label="" prop="name" required>
                  <el-input v-model="ruleForm.name" ></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer"  >
            <el-button @click="editVisible = false"> </el-button>
            <el-button type="primary" @click="saveEdit('ruleForm')"> </el-button>
        </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delVisible" width="300px" center>
        <div class="del-dialog-cnt"></div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="delVisible = false"> </el-button>
            <el-button type="primary" @click="deleteRow"> </el-button>
        </span>
    </el-dialog>
   
   <!--  -->
    <el-dialog title="" :visible.sync="visible" width="40%"  >
        <el-form ref="ruleForm"   label-width="120px" v-loading="loading1"  >
            <el-form-item label=""  >
                  <el-upload
                    ref="upload"
                    class="upload-demo"
                    accept=".xls"
                    :limit="1"
                    :auto-upload="false"
                    action="https://jsonplaceholder.typicode.com/posts/"
                   >
                    <el-button size="small" type="primary"></el-button>
                  </el-upload>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer"  >
            <el-button @click="visible = false"> </el-button>
            <el-button type="primary" @click="uploads"> </el-button>
        </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="codeVisible" width="40%"  >
        <div class="flex" style="height:120px;background:#FEFF00;">
                <div style="margin-top: 2px;" ><v-Code :name="codeText" ></v-Code></div>
                <div style=" color: #000;padding-left:20px">
                    <div style="margin: 5px 0px 18px; font-size: 18px;font-weight: 600;">{{codeText.name}}</div>
                    <div></div>
                    <div>yiliaofeiwuzhuisu</div>
                </div>
        </div>
        <span slot="footer" class="dialog-footer"  >
            <el-button type="primary"  @click="codeVisible = false"> </el-button>
        </span>
    </el-dialog>

  </div>
</template>

<script>
const moment = require("moment");
import vCode from '../../common/qrcode.vue';
export default {
  data() {
    return {
      loading: false,
      loading1: false,
      delVisible: false,
      visible: false,
      codeVisible: false,
      codeText:'',
      kName:'',
      mTitle:'',
      tableData: [
        {
          id: 1,
          name: "",
          time: "2018-10-08 14:44",
        }
      ],
      total: 0,
      cur_page: 1,
      editVisible:false,
      ruleForm:{
        name:'',
      },
      rules: {
          name: [
              { required: true, message: '' }
          ],
      },
      users:[
        {name:'',value:'1'},
        {name:'',value:'2'},
        {name:'',value:'3'},
        {name:'',value:'4'},
        {name:'',value:'5'},
      ]
    };
  },
  components:{
         vCode
  },
  methods: {
    // 
    handleCurrentChange(val) {
      this.cur_page = val;
      this.getData()
    },
    // 
    search() {
      console.log(this.date);
    },
    openAdd(type,row){
         this.loading1=false;
      if(type==1){
        this.mTitle='';
        this.ruleForm={
             name:'',
        };
        if(this.$refs['ruleForm']){
           this.$refs['ruleForm'].resetFields();
        }
      }else{
        this.mTitle='';
        var form=this.ruleForm;
        for(var key in row){
          form[key]=row[key]
        }
        this.ruleForm=form;
      }
      this.editVisible=true;
    },
    detials(index,row){
      this.openAdd(2,row);
    },
    // 
    saveEdit(formName) {
        this.$refs[formName].validate((valid) => {
            if (valid) {
              if(this.mTitle==''){
                this.add()
              }else{
                this.edit()
              }
            } else {
                return false;
            }
        });
    },
    // 
    add(){
        this.loading1=true;
        this.$axios({
              method:'post',
              url:'/platform/hospital/department/addDo',
              data:{
                  name:this.ruleForm.name,
              }
          }).then((res) =>{
              if(res.data.code==0){
              this.loading1=false;
              this.editVisible = false;
              this.getData()
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
          })
    },
    // 
    edit(){
        this.loading1=true;
        this.$axios({
              method:'put',
              url:'/platform/hospital/department/editDo',
              data:this.ruleForm
          }).then((res) =>{
              if(res.data.code==0){
              this.loading1=false;
              this.editVisible = false;
              this.getData()
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
        })
    },
    // 
    deleteRow(){
        this.$axios({
              method:'DELETE',
              url:'/platform/hospital/department/delete?ids='+this.id,
          }).then((res) =>{
              if(res.status==200){
                  this.$message.success('');
                  this.delVisible=false;
                  this.getData();
              }else{
                  this.$message.error(res.data.msg);
              }
          }).catch((error) =>{
              console.log(error)    
        })
    },
    deal(index,row){
      this.id=row.id;
      this.delVisible=true;
    },
    exportCode(){
      console.log(11)
    },
    upload(){
      this.visible=true
    },
    uploads(){
      this.$refs.upload.submit();
    },
    qrCode(index,row){
        this.codeText={id:row.id,name:row.name}
        this.codeVisible=true;
    },
    getData(){
        this.loading=true;
        this.$axios({
            method:'get',
            url:'/platform/hospital/department/listPage?pageNumber='+this.cur_page+'&pageSize=10&name='+this.kName,
        }).then((res) =>{
            if(res.status==200){
                this.loading=false;
                this.tableData=res.data.list.map((ele)=>{
                    ele.time=moment(ele.opAt).format('YYYY-MM-DD HH:mm:ss')
                    return ele
                });
                this.total=res.data.totalCount;
            }else{
                this.$message.error(res.data.msg);
            }
        }).catch((error) =>{
            console.log(error)    
        })
    }
  },
  mounted() {
    this.getData()
  }
};
</script>


<style scoped>
.cloudBox > div {
  text-align: center;
  box-sizing: border-box;
  overflow: hidden;
  min-height: 300px;
  position: relative;
}

.upload-demo /deep/ .el-upload--text{
  height:40px;
  width:100%;
}

</style>




